/* stylelint-disable at-rule-empty-line-before,at-rule-name-space-after,at-rule-no-unknown */
.la-notification {
  position: fixed;
  color: #ffffff;
  font-size: 12px;
  left: 8px;
  top: 8px;
  right: 8px;
  z-index: 1009;
}
.la-notification .content {
  padding: 8px 30px 8px 20px;
  position: absolute;
  left: 0;
  right: 0;
  opacity: 0;
  color: inherit;
  text-decoration: none;
  visibility: hidden;
  transition: all 0.6s;
  border-radius: 3px;
  text-shadow: 0 0 3px rgba(40, 42, 60, 0.3);
  background-color: transparent;
  line-height: 150%;
  font-size: 14px;
}
.la-notification.active .content.warn {
  background-color: #ffb822;
}
.la-notification.active .content.error {
  background-color: #fd397a;
}
.la-notification.active .content.default {
  background-color: #5d78ff;
}
.la-notification.active .content.success {
  background-color: #0abb87;
}
.la-notification.active .content.dark {
  background-color: #48465b;
}
.la-notification.active .content {
  opacity: 1;
  visibility: visible;
}
.la-notification.active .close {
  position: absolute;
  right: 14px;
  top: 12px;
  display: inline-block;
  width: 14px;
  height: 14px;
  overflow: hidden;
  cursor: pointer;
}
.la-notification.active .close::before,
.la-notification.active .close::after {
  content: '';
  position: absolute;
  height: 1px;
  width: 100%;
  top: 50%;
  left: 0;
  margin-top: -1px;
  background: #ffffff;
}
.la-notification.active .close::before {
  transform: rotate(45deg);
}
.la-notification.active .close::after {
  transform: rotate(-45deg);
}
